import "../styles/editor.scss";
import { useBlockProps } from "@wordpress/block-editor";
import { useEffect } from "@wordpress/element";
import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { prefix } from "../../../constants";
import { withInstanceId, compose } from "@wordpress/compose";
import InspectorPanel from "./inspector";
import { getTypoStyle, getResponsiveRangeVal, isSingleProductTemplate, getDimensionStyle } from "../../../helpers";

const Edit = (props) => {

	const {
		clientId,
		instanceId,
		setAttributes,
		attributes: {
			icon,
			textColor,
			iconColor,
			iconSize,
			textTypo,
			textMargin
		},
	} = props;

	const blockProps = useBlockProps();

    useEffect(() => {
        setAttributes({
            block_id: `${prefix}-usp-${blockProps.id}`,
        });
    }, []);

	const iconStyle = {
		fontSize: getResponsiveRangeVal(iconSize),
		color: iconColor
	};

	const textStyle = {
		...getTypoStyle(textTypo),
		color: textColor,
		...getDimensionStyle("margin", textMargin),
	};

	return (
	    <>
	        {isSingleProductTemplate() ? (
	            <>
	                <InspectorPanel {...props} />
	                <div {...blockProps}>
	                    <p> {__("Product Unique Selling Points", "wc-booster")} </p>
	                    <ul className="wc-booster-usps">
	                        <li><i className={`fa ${icon.icon ? icon.icon : icon}`} style={iconStyle}></i><span style={textStyle}>{__("Free shipping on orders over $50", "wc-booster")}</span></li>
	                        <li><i className={`fa ${icon.icon ? icon.icon : icon}`} style={iconStyle}></i><span style={textStyle}>{__("Buy 2, get 1 free", "wc-booster")}</span></li>
	                        <li><i className={`fa ${icon.icon ? icon.icon : icon}`} style={iconStyle}></i><span style={textStyle}>{__("20% off all products", "wc-booster")}</span></li>
	                    </ul>
	                </div>
	            </>
	        ) : (
	        	<div {...blockProps}>
		            <div
		            	className="wc-booster-single-product-block-notice">
		            	{__('This block is intended for the single product template only.', 'woo-booster')}
		            </div>
	            </div>
	        )}
	    </>
	);
};

export default compose(withInstanceId)(Edit);
