import { PanelColor } from "../../../custom-control";
import ResponsiveRangeWrapperControl from "../../../custom-control/responsive-range-wrapper";

import { TextControl, PanelBody, BaseControl } from "@wordpress/components";

import { InspectorControls } from "@wordpress/block-editor";

import { __ } from "@wordpress/i18n";

import Typography from "../../../custom-control/typography";

import { faIcons } from "../../../constants";

import metadata from "../block.json";

import { getDefaultAttr } from "../../../helpers";

import IconSelector from "../../../custom-control/icon-selector";

import Dimension from "../../../custom-control/dimension";

const InspectorPanel = (props) => {
	const {
		attributes: {
			icon,
			iconSize,
			iconColor,
			textColor,
			textTypo,
			textMargin
		},
		setAttributes,
	} = props;

	const def = getDefaultAttr(metadata);

	return (
		<>
			<InspectorControls>
				<PanelBody title={__("General", "wc-booster")} initialOpen={false}>

					<BaseControl label={__("Icon", "wc-booster")}>
						<IconSelector
							value={icon}
							icons={faIcons}
							prefix="fa"
							label={__("Select an Icon", "wc-booster")}
							onChange={(newIcon) => {
								setAttributes({ icon: newIcon });
							}}
						/>
					</BaseControl>

					<ResponsiveRangeWrapperControl
						label={__("Icon Size", "wc-booster")}
						defaultValue={def("iconSize")}
						value={iconSize}
						onChange={(iconSize) => setAttributes({ iconSize })}
						beforeIcon=""
					/>

				</PanelBody>
				<PanelBody title={__("Color", "wc-booster")} initialOpen={false}>
					<PanelColor
						colorSettings={[
							{
								label: __("Icon Color"),
								value: iconColor,
								onChange: (iconColor) => setAttributes({ iconColor }),
							},
							{
								label: __("Text Color"),
								value: textColor,
								onChange: (textColor) => setAttributes({ textColor }),
							}
						]}
					/>
				</PanelBody>
				<PanelBody title={__("Typography", "wc-booster")} initialOpen={false}>
					<Typography
						label={__("Text", "wc-booster")}
						value={textTypo}
						defaultValue={metadata.attributes.textTypo.default}
						onChange={(textTypo) => setAttributes({ textTypo })}
					/>
				</PanelBody>

				<PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
                    <Dimension
                        label={__("Text Margin", "wc-booster")}
                        value={textMargin}
                        defaultValue={metadata.attributes.textMargin.default}
                        onChange={(textMargin) => setAttributes({ textMargin })}
                    />
                </PanelBody>
			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
